html {
    box-sizing: border-box;
    font-size: 16px;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
address,
img,
q,
b,
u,
i,
ol,
ul,
li,
form,
label,
footer,
header,
nav,
section,
button,
input,
textarea {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

footer,
header,
nav,
section {
    display: block;
}

input,
textarea {
    color: inherit;
    white-space: normal;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    transition: background-color 5000s ease-in-out 0s;
    background: -webkit-linear-gradient(
        top,
        rgba(255, 255, 255, 0) 0%,
        rgba(0, 174, 255, 0.04) 50%,
        rgba(255, 255, 255, 0) 51%,
        rgba(0, 174, 255, 0.03) 100%
    );
}

ul,
ol {
    list-style: none;
}

@media (prefers-reduced-motion: reduce) {
    * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

img {
    max-width: 100%;
    display: block;
}

a {
    text-decoration: none;
    color: inherit;
}

button {
    background-color: transparent;
    cursor: pointer;
}

body {
    font-family: 'Gilroy', sans-serif;
    color: $text;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 600;
    background-color: #f7f7f7;
}

.container {
    @include container(1170px);
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.title {
    @extend %grotesk-400;
    font-size: 48px;
}

.button {
    position: relative;
    display: inline-block;
    @extend %gilroy-600;
    font-size: 14px;
    color: $accent;
    font-weight: 600;
    text-transform: uppercase;
    border: 2px solid $accent;
    border-radius: 90px;

    &--follow,
    &--follow-clear {
        background-image: url('../img/follow.svg');
    }

    &--follow {
        @include bg(15px, calc(100% - 20px));
    }

    &--follow-clear {
        padding-right: 28px;
        border: none;

        @include bg(15px, calc(100% - 3px));
    }
}

.follow {
    position: relative;
    text-transform: uppercase;
    font-size: 14px;

    &::after {
        position: absolute;
        right: -28px;
        top: 50%;
        transform: translateY(-56%);
        width: 18px;
        height: 18px;
        background-image: url('../img/decor-icons/plus-icon.svg');
        content: '';
    }

    &--accent {
        color: $accent;

        &::after {
            background-image: url('../img/decor-icons/plus-icon-accent.svg');
        }
    }
}
